import React, { useState, useEffect, useRef } from 'react';
import { Col, Row, Container } from 'react-bootstrap';
import { Jumbotron } from './migration';
import { Icon } from '@iconify/react';

const TechSkills = () => {
  const icons = [
    { name: 'AWS', icon: 'skill-icons:aws-light' },
    { name: 'Azure', icon: 'skill-icons:azure-light' },
    { name: 'PowerShell', icon: 'skill-icons:powershell-light' },
    { name: 'Linux', icon: 'skill-icons:linux-light' },
    { name: 'Bash', icon: 'skill-icons:bash-light' },
    { name: 'React.js', icon: 'skill-icons:react-light' },
    { name: 'JavaScript', icon: 'skill-icons:javascript' },
    { name: 'Node.js', icon: 'skill-icons:nodejs-light' },
    { name: 'Docker', icon: 'skill-icons:docker' },
    { name: 'MySQL', icon: 'skill-icons:mysql-light' },
    { name: 'Git', icon: 'skill-icons:git' },
    { name: 'GitHub', icon: 'skill-icons:github-light' },
    { name: 'GitLab', icon: 'skill-icons:gitlab-light' },
    { name: 'GraphQL', icon: 'skill-icons:graphql-light' },
    { name: 'Postman', icon: 'skill-icons:postman' },
    { name: 'WordPress', icon: 'skill-icons:wordpress' },
    { name: 'Cloudflare', icon: 'skill-icons:cloudflare-light' },
    { name: 'Grafana', icon: 'skill-icons:grafana-light' }
  ];
  const [visibleIcons, setVisibleIcons] = useState([]);
  const [isAllVisible, setIsAllVisible] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const sectionRef = useRef(null);

  useEffect(() => {
    if (window.innerWidth < 992) {
      setIsMobile(true);
    }
  }, []);

  const IconCard = ({ icon, name }) => (
    <div className="icon-card p-3 text-center rounded" style={{ backgroundColor: '#f2f2f2' }}>
      <Icon icon={icon} height={60} />
      <p className="m-2">{name}</p>
    </div>
  );

  useEffect(() => {
    if (!isMobile) {
      setVisibleIcons(icons);
      setIsAllVisible(true);
    } else {
      setVisibleIcons(icons.slice(0, 6));
      setIsAllVisible(false);
    } // eslint-disable-next-line
  }, [isMobile]);

  const handleClick = () => {
    setVisibleIcons(icons);
    setIsAllVisible(!isAllVisible);
    sectionRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
  };

  return (
    <section id="skills" ref={sectionRef}>
      <Container>
        <Jumbotron fluid className="bg-white">
          <h2 className="display-4 mb-5 text-center">Tech Skills</h2>
          <Row>
            {visibleIcons.slice(0, isAllVisible ? icons.length : 6).map(({ name, icon }, index) => (
              <Col xs={6} sm={4} md={2} className="mb-4" key={index}>
                <IconCard icon={icon} name={name} />
              </Col>
            ))}
          </Row>
          {isMobile && (
            <Row>
              <Col className="text-center">
                <button className="btn btn-outline-dark btn-lg" onClick={handleClick}>
                  {isAllVisible ? 'Show Less' : 'Show More'}
                </button>
              </Col>
            </Row>
          )}
        </Jumbotron>
      </Container>
    </section>
  );
};

export default TechSkills;
