import React, { useState, useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import { Jumbotron } from './migration';
import Row from 'react-bootstrap/Row';
import { Col, Badge, Table } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import Skeleton from 'react-loading-skeleton';
import moment from 'moment';
import { getMediumPost } from '@arifszn/blog-js';

const Blogs = () => {
  const [articles, setArticles] = useState([]);

  useEffect(() => {
    getMediumPost({
      user: 'rauniyarankit'
    }).then((res) => {
      setArticles(res);
    });
  }, []);

  return (
    <Jumbotron fluid id="blogs" className="bg-light m-0">
      <Container className="">
        <h2 className="display-4 pb-5 text-center">Blogs</h2>
        <Row>
          {articles.map((article, index) => (
            <Col md={6} key={article.id || index}>
              <a
                style={{ textDecoration: 'none', color: 'black' }}
                href={article.link}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Card className="card shadow-lg p-3 mb-5 bg-white rounded">
                  <Card.Body>
                    <Card.Title as="h5" style={{ color: 'inherit' }}>
                      {article.title || <Skeleton />}
                    </Card.Title>
                    <Card.Text style={{ color: 'inherit' }}>
                      {!article.description ? '' : article.description.split('.')[0]}.
                    </Card.Text>
                    <Table style={{ color: 'black' }}>
                      <tbody>
                        <tr>
                          <td>Categories:</td>
                          <td>
                            <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                              {article.categories ? (
                                article.categories.map((category) => (
                                  <Badge
                                    className="badge bg-dark"
                                    key={category}
                                    variant="secondary"
                                    style={{ margin: '2px' }}
                                  >
                                    {category}
                                  </Badge>
                                ))
                              ) : (
                                <Skeleton count={3} />
                              )}
                            </div>
                          </td>
                        </tr>
                        <tr>
                          <td>Published:</td>
                          <td>
                            {!article.publishedAt
                              ? ''
                              : moment(article.publishedAt).format('D MMM, YYYY') || (
                                  <Skeleton count={1} />
                                )}
                          </td>
                        </tr>
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </a>
            </Col>
          ))}
        </Row>
      </Container>
    </Jumbotron>
  );
};

export default Blogs;
